body {
  background-color: #3d3d3d;
}

.bg-primary {
  font-size: 16pt;
  color: #ffffff;
  text-align: left;
  padding: 1.5vw 0 1.5vw 8vw;
}

.body-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.btn-primary {
  margin: 2vw 0 0 8vw;
}

.btn-success{
  margin: 2vw 0 0 3vw;
}

.listOne{
  margin: 1vw 0 0 2vw;
  font-size: 1.5vw;
  /*color: #ffffff;*/
}

.titleFlex{
  display: -ms-flexbox;
  display: flex;
}

.containerItem{
  /*width: 65%;*/
}

.container-fluid{
  width: 65%;
}

.itemsBoxes{
  width: 80%;
  margin: 0.5vw 0 0 2.5vw;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  backgroundColor: this.state.color;
}

.title {
  -webkit-transition: background-color 0.9s ease-in;
  -o-transition: background-color 0.9s ease-in;
  transition: background-color 0.9s ease-in;
  border-radius: 15px;
}

audio {
  width: 60vw;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



